import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  {
    path: 'home/:section',
    loadChildren: () => import('./home/home.module').then( m => m.HomePageModule)
  },
  {
    path: '',
    redirectTo: 'home/home',
    pathMatch: 'full'
  },
  {
    path: 'solido/:section/:personajeSeleccionado',
    loadChildren: () => import('./pages/solido/solido.module').then( m => m.SolidoPageModule)
  },
  {
    path: 'liquido',
    loadChildren: () => import('./pages/liquido/liquido.module').then( m => m.LiquidoPageModule)
  },
  {
    path: 'gaseoso/:personajeSeleccionado',
    loadChildren: () => import('./pages/gaseoso/gaseoso.module').then( m => m.GaseosoPageModule)
  },
  {
    path: 'fisico/:section',
    loadChildren: () => import('./pages/tipo/fisico/fisico.module').then( m => m.FisicoPageModule)
  },
  {
    path: 'virtual',
    loadChildren: () => import('./pages/tipo/virtual/virtual.module').then( m => m.VirtualPageModule)
  },
  {
    path: 'scanner',
    loadChildren: () => import('./pages/scanner/scanner.module').then( m => m.ScannerPageModule)
  },
  {
    path: 'mvirtual',
    loadChildren: () => import('./pages/mvirtual/mvirtual.module').then( m => m.MvirtualPageModule)
  },
  {
    path: 'pdf',
    loadChildren: () => import('./pages/pdf/pdf.module').then( m => m.PdfPageModule)
  },
  {
    path: 'img/:numero',
    loadChildren: () => import('./pages/img/img.module').then( m => m.ImgPageModule)
  },



];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
